@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&display=swap);
body {
  margin: 0px !important;
  padding: 0px !important;

  overflow: hidden;
}

#timonerie #book {
    position: absolute;
    top: 47%;
    left: 39%;
    width: 5%;

    z-index: 4;
}

#timonerie #book svg {
    max-width: 100%;
}

#timonerie #book svg:hover {
    outline: 2px solid white;
    /* box-shadow: 2px 2px 2px 2px white; */
}
#timonerie {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    
    margin: auto;
}

#timonerie #timonerieSVG{
    height: 100vh;
    max-width: 100vw;

    background-image: url('http://t2.gstatic.com/images?q=tbn:ANd9GcTBDXtANxxJTGfiXkgGQKuwZp6eZy1lYzlvoYHXw7aYlF2igYBbCjz8oM2D7X-wrRJpBWxbSVHZsr_J1EhVr88');

    z-index: 1;
}

#timonerie #GPS {
    position: absolute;
    top: 47%;
    left: 45%;
    width: 10% !important;
    height: 10% !important;
    overflow-y: hidden;

    z-index: 4;
}

#timonerie #GPS:hover {
    outline: 2px solid white;
}


#timonerie #GPS iframe {
    pointer-events: none;
    margin-top: -20%;
}

#timonerie #Map {
    position: absolute;
    top: 54%;
    left: 61%;
    width: 13%;
    transform: rotate(35.5deg);

    z-index: 4;
}

#timonerie #Map img {
    width: 100%;
}

#timonerie #Map:hover {
    outline: 2px solid white;
}

#timonerie #marin {
    position: absolute;
    top: 40%;

    transform-origin: center;

    z-index: 5;
}

#timonerie #marin svg {
    height: 50vh;
}

#timonerie #marin.movingLeft svg {
    transform: scale(1, 1);
}

#timonerie #marin.movingRight svg {
    transform: scale(-1, 1);
}

#timonerie #marin.movingLeft #leftLeg, #timonerie #marin.movingRight #leftLeg {
    -webkit-animation-name: rotation;
            animation-name: rotation;
    -webkit-animation-duration: 2.5s;
            animation-duration: 2.5s;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;

    transform-origin: top center;
    transform-box: fill-box;
   
}

#timonerie #marin.movingLeft #rightLeg, #timonerie #marin.movingRight #rightLeg {
    -webkit-animation-name: rotation;
            animation-name: rotation;
    -webkit-animation-duration: 2.5s;
            animation-duration: 2.5s;
    -webkit-animation-delay: -1.25s;
            animation-delay: -1.25s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;

    transform-origin: top center;
    transform-box: fill-box;
}

@-webkit-keyframes rotation {
    0% {
        transform: rotateZ(-0deg);
    }
    25% {
        transform: rotateZ(-10deg);
    }
    50% {
        transform: rotateZ(0deg);
    }
    75% {
        transform: rotateZ(10deg);
    }
    100% {
        transform: rotateZ(0deg);
    }
}

@keyframes rotation {
    0% {
        transform: rotateZ(-0deg);
    }
    25% {
        transform: rotateZ(-10deg);
    }
    50% {
        transform: rotateZ(0deg);
    }
    75% {
        transform: rotateZ(10deg);
    }
    100% {
        transform: rotateZ(0deg);
    }
}



#OpenedBook {
    width: 95%;
    height: 100%;

    padding: 2vh 10vw 2vh 10vw; 

    background-color: #6C0303;

    display: flex;
    flex-direction: row;
}

#OpenedBook .Page {
    width: 40vw;
    height: 80vh;

    font-size: 1.35vw;;
    font-family: 'Dancing Script', cursive;

    display: inline-block;

    background-color: white;

    border-radius: 1%;

    padding: 5%;
}

#OpenedBook .Page h1 {
    font-size: 5vw;
    text-align: center;
}

.sep {
    width: 1px;
    height: 80vh;
    background-color: black;
}

.arrow {
    position: absolute;
    width: 5vw;
    
    top: 45%;
    left: 9%;
}

.arrow#leftArrow {
    transform: scale(-0.5, 1);
}

.arrow#rightArrow {
    transform: scale(0.5, 1);
    left: 86%;
}
#Details {
    width: 100vw;
    height: 100vh;
    position: absolute;
    z-index: 1000;

    background-color: #2E2E2E;

    display: flex;
}

#Details #marinetraffic{
    padding: 5%;
}

#Details #leave {
    width: 4%;
    position: absolute;
    top: 0.5%;
    right: 0.5%;
}
#Main {
    background-color: #2E2E2E;
    display: flex;
}
